@charset "UTF-8";
html {
  font-size: 62.5%;
}

body {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.6rem;
  line-height: 1.6;
  color: #1a1a1a;
}

/* すべての要素のマージン・パディングをリセット */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* HTML5の要素のデフォルトスタイルを打ち消す */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block;
}

/* ボディの基本スタイル */
body {
  line-height: 1.5;
  font-family: sans-serif;
  background-color: #fff;
  color: #000;
}

/* リストのデフォルトスタイルを除去 */
ol,
ul {
  list-style: none;
}

/* テーブルのセル間のスペースを除去 */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* 見出しのデフォルトスタイルをリセット */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

/* フォーム系要素のリセット */
input,
button,
textarea,
select {
  font: inherit;
  color: inherit;
  background: none;
  border: none;
  outline: none;
  padding: 0;
  margin: 0;
}

/* リンクのスタイルリセット */
a {
  text-decoration: none;
  color: inherit;
}

/* 画像のデフォルトの余白を削除 */
img {
  display: block;
  max-width: 100%;
  height: auto;
}

body {
