.loading-spinner {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #fff;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.loading-spinner.is-loading {
  display: flex;
}
.loading-spinner.is-hidden {
  display: none;
}
.loading-spinner .loading-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
}
.loading-spinner .wave-loader {
  display: block;
  width: 100%;
  max-width: 480px;
  height: auto;
  mask-image: linear-gradient(to right, transparent 0%, transparent 30%, black 45%, black 55%, transparent 70%, transparent 100%);
  mask-size: 250% 100%;
  mask-repeat: no-repeat;
  animation: loading-highlight-pass 1.6s linear infinite;
  opacity: 0.5;
}
.loading-spinner .loading-text {
  display: flex;
  justify-content: center;
  gap: 0.2em;
  margin-top: 1.5rem;
  font-size: 2rem;
  font-weight: bold;
  font-family: "Roboto", sans-serif;
  letter-spacing: 0.05em;
  color: #b6c8db;
}
.loading-spinner .loading-text span {
  opacity: 0;
  transform: translateY(10px);
  animation: fadeInUpLetter 0.6s ease forwards;
}
.loading-spinner .loading-text span:nth-child(1) {
  animation-delay: 0.1s;
}
.loading-spinner .loading-text span:nth-child(2) {
  animation-delay: 0.2s;
}
.loading-spinner .loading-text span:nth-child(3) {
  animation-delay: 0.3s;
}
.loading-spinner .loading-text span:nth-child(4) {
  animation-delay: 0.4s;
}
.loading-spinner .loading-text span:nth-child(5) {
  animation-delay: 0.5s;
}
.loading-spinner .loading-text span:nth-child(6) {
  animation-delay: 0.6s;
}
.loading-spinner .loading-text span:nth-child(7) {
  animation-delay: 0.7s;
}
.loading-spinner .loading-text span:nth-child(8) {
  animation-delay: 0.8s;
}
.loading-spinner .loading-text span:nth-child(9) {
  animation-delay: 0.9s;
}
.loading-spinner .loading-text span:nth-child(10) {
  animation-delay: 1s;
}
@keyframes fadeInUpLetter {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes loading-highlight-pass {
  0% {
    mask-position: 125% 0;
  }
  100% {
    mask-position: -125% 0;
  }
}
@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.image-modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 10000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.85);
  box-sizing: border-box;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.4s cubic-bezier(0.4, 0, 0.2, 1), visibility 0s linear 0.4s;
}
.image-modal.is-visible {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transition-delay: 0s;
}
.image-modal__content {
  margin: 0 auto;
  display: block;
  max-width: 90%;
  max-height: 85vh;
  transform: scale(0.8);
  opacity: 0;
  transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1) 0.05s, opacity 0.3s ease 0.05s;
  will-change: transform, opacity;
}
.image-modal.is-visible .image-modal__content {
  transform: scale(1);
  opacity: 1;
}
.image-modal__caption {
  margin: 15px auto 0;
  display: block;
  width: 80%;
  max-width: 700px;
  text-align: center;
  color: #ccc;
  padding: 10px 0;
  font-size: 0.9em;
  opacity: 0;
  transition: opacity 0.3s ease 0.2s;
}
.image-modal.is-visible .image-modal__caption {
  opacity: 1;
}
.image-modal__close-button {
  position: absolute;
  top: 20px;
  right: 35px;
  color: #f1f1f1;
  font-size: 40px;
  font-weight: bold;
  transition: color 0.3s ease, opacity 0.3s ease 0.2s;
  cursor: pointer;
  opacity: 0;
}
.image-modal__close-button:hover, .image-modal__close-button:focus {
  color: #bbb;
  text-decoration: none;
}
.image-modal.is-visible .image-modal__close-button {
  opacity: 1;
}

