.fleet-item-slider {
  position: relative;
}
.fleet-item-slider .splide__slide {
  max-height: calc(100vh - 152px - 10.68rem);
}
.fleet-item-slider .splide__slide img,
.fleet-item-slider .splide__slide picture {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.fleet-item-slider.is-single-slide .splide__arrows,
.fleet-item-slider.is-single-slide .splide__pagination {
  display: none !important;
}
.fleet-item-slider .splide__arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  background: rgba(0, 56, 112, 0.3);
  border: none;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 0;
  opacity: 0.7;
  transition: opacity 0.3s ease, background-color 0.3s ease;
}
.fleet-item-slider .splide__arrow svg {
  fill: #fff;
  width: 20px;
  height: 20px;
}
.fleet-item-slider .splide__arrow--prev {
  left: 15px;
}
.fleet-item-slider .splide__arrow--prev svg {
  transform: rotate(180deg);
}
.fleet-item-slider .splide__arrow--next {
  right: 15px;
}
.fleet-item-slider .splide__arrow:hover {
  background: rgba(0, 56, 112, 0.6);
  opacity: 1;
}
.fleet-item-slider .splide__arrow:focus {
  outline: 2px solid Highlight;
  outline: 2px solid -webkit-focus-ring-color;
}
.fleet-item-slider .splide__arrow:focus:not(:focus-visible) {
  outline: none;
}
.fleet-item-slider .splide__arrow:focus-visible {
  outline: 2px solid Highlight;
  outline: 2px solid -webkit-focus-ring-color;
}
.fleet-item-slider .splide__arrow:disabled {
  opacity: 0.3;
  cursor: default;
  background: rgba(0, 56, 112, 0.1);
}
.fleet-item-slider .splide__pagination {
  bottom: calc(1em + 10px + 10px);
  margin-top: 10px;
}
.fleet-item-slider .splide__pagination li {
  margin: 0 5px;
}
.fleet-item-slider .splide__pagination__page {
  background: #1a1a1a;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  opacity: 0.6;
  transition: transform 0.2s ease-out, background-color 0.2s ease-out, opacity 0.2s ease-out;
  border: none;
  padding: 0;
  margin: 0;
}
.fleet-item-slider .splide__pagination__page:hover {
  background: #003870;
  opacity: 0.8;
  transform: scale(1.1);
}
.fleet-item-slider .splide__pagination__page.is-active {
  background: #005cac;
  transform: scale(1.2);
  opacity: 1;
}
.fleet-item-slider .splide__pagination__page:focus {
  outline: 2px solid Highlight;
  outline: 2px solid -webkit-focus-ring-color;
  outline-offset: 2px;
}
.fleet-item-slider .splide__pagination__page:focus:not(:focus-visible) {
  outline: none;
}
.fleet-item-slider .splide__pagination__page:focus-visible {
  outline: 2px solid Highlight;
  outline: 2px solid -webkit-focus-ring-color;
  outline-offset: 2px;
}

